	data() {
		return {
			avatar: uni.getStorageSync('login_key').avatar,
			qiandao: true,
			qiandao2: false,
			login:false,
			userId:uni.getStorageSync('login_key').userId
		};
	},
	onLoad: function() {},
	onShow: function() {
		this.login= uni.getStorageSync('login_key').login;
	},
	methods: {
		goqiandao: function() {
			var _this=this;
			if (uni.getStorageSync('login_key').login === true) {
				var newScore=uni.getStorageSync('login_key').score+5;
				uni.request({
					url: this.apiServer +'/user/score',
					method: 'post',
					header: { 'content-type': 'application/x-www-form-urlencoded' },
					data: {
						userId: _this.userId,
						score: newScore
					},
					success: resd => {
						console.log(resd.data);
						this.qiandao = false;
						this.qiandao2 = true;
						uni.request({
							url: this.apiServer +'/user/' + uni.getStorageSync('login_key').userId,
							method: 'GET',
							data: {
								userId: _this.userId
							},
							header: {
								'content-type': 'application/json'
							},
							success: res => {
								if (res.data.code == 0) {
									//将用户数据记录在本地存储
									uni.setStorageSync('login_key', {
										userId: res.data.data.id,
										nickname: res.data.data.nickname,
										avatar: res.data.data.avatar,
										token: res.data.data.token,
										score:res.data.data.score,
										mobile: res.data.data.mobile,
										login: true
									});
								}
							}
						});
					}
				});
			} else {
				uni.showModal({
					title: '提示',
					content: '您未登录',
					cancelText: '再等等',
					confirmText: '前往登录',
					success: function(res) {
						if (res.confirm) {
							uni.navigateTo({
								url: '../signin/signin'
							});
						}
					}
				});
			}
		},
		goIndex: function() {
			if (uni.getStorageSync('login_key').login === true) {
				uni.switchTab({
					url: '../index/index'
				});
			} else {
				uni.showModal({
					title: '提示',
					content: '您未登录',
					cancelText: '再等等',
					confirmText: '前往登录',
					success: function(res) {
						if (res.confirm) {
							uni.navigateTo({
								url: '../signin/signin'
							});
						}
					}
				});
			}
		},
		goSignin:function (){
			uni.navigateTo({
				url:'../signin/signin'
			})
		},
		close: function() {
			this.qiandao2 = false;
		}
	}
};
